/*         MENU             */
/* Style The Dropdown Button */

.dropbtn {

    background-color: #501DFF;
  
    color: white;
  
    padding: 0.5rem 2rem 0.5rem 2rem;
  
    font-size: 16px;
  
    border: none;
  
    cursor: pointer;
    
    border-bottom: 6px inset rgba(0,0,0,.5);
	
    border-left: 6px inset rgba(0,0,0,.5);
	
    border-right: 6px inset rgba(255,255,255,.5);
	
    border-top: 6px inset rgba(255,255,255,.5);
  
    font-family: 'VT323', monospace ;
  
    font-size: 30px;

    text-transform: uppercase;

  
  }

  @media screen and (max-width:600px) {
    .dropbtn {
      font-size: 18px;
    }
  }
  @media screen and (max-width:520px) {
    .dropbtn {
      padding: 0.3rem 1rem 0.3rem 1rem;
    }
  }
    
    /* The container <div> - needed to position the dropdown content */
  
    .dropdown {
  
      position: relative;
  
      display: inline-block;
  
    }
    
    /* Dropdown-content from the menu button */
  
    .dropdown-content {
  
      display: none;
  
      position: absolute;
  
      background-color: #0f0f12;
  
      min-width: 160px;
  
      box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  
      z-index: 1;
  
  
    }
    
    /* Links to the other pages in the dropdown menu */
  
    .dropdown-content a {
  
      color: white;
  
      padding: 12px 16px;
  
      text-decoration: none;
  
      display: block;
  
      font-family: sans-serif;
  
    }
    
    /* Change the color when you hover over the menu */
  
    .dropdown-content a:hover {background-color: #521dff7e}
    
    /* Show the dropdown menu on hover */
  
    .dropdown:hover .dropdown-content {
  
      display: block;
  
    }
    
    /* Change the background color of the dropdown button when the dropdown content is shown */
  
    .dropdown:hover .dropbtn {
  
      background-color: #521dff7e;
  
    }

    .ol{
      color: #FAFAFA;
  }
